import React from 'react';
import {
    Switch,
    Route,
    useRouteMatch,
    Redirect,
  } from "react-router-dom";
  
import { CustomDrawer } from '../components';
import { Home, Servico, Cartao, Perfil, Agenda, Conta, Solicitacao } from '../screens';

import { APPURL } from '../environment';

import { LoadContext } from '../contexts';

import { useBackdrop } from '../hooks';

const Dashboard = () => {

    const [open, close, Backdrop] = useBackdrop()

    let match = useRouteMatch();
            return (
                <LoadContext.Provider value={{open, close}}>
                <Switch>
                    <Route path="/co">
                        <CustomDrawer customStyle={{
                            padding: 5,
                            height: '100vh',
                        }}>
                            <Conta />
                        </CustomDrawer>
                    </Route>
                    <Route path="/ct">
                        <CustomDrawer customStyle={{
                            padding: 5,
                            height: '100vh',
                        }}>
                            <Cartao />
                        </CustomDrawer>
                    </Route>
                    <Route path="/a">
                        <CustomDrawer customStyle={{
                            padding: 0,
                            height: '100vh',
                        }} >
                            <Agenda />
                        </CustomDrawer>
                    </Route>
                    <Route path="/p">
                        <CustomDrawer customStyle={{
                            padding: 5,
                            height: '100vh',
                        }}>
                            <Perfil />
                        </CustomDrawer>
                    </Route>
                    <Route path="/so">
                        <CustomDrawer customStyle={{
                            padding: 5,
                            height: '100vh',
                        }}>
                            <Solicitacao />
                        </CustomDrawer>
                    </Route>
                    <Route path="/s" >
                        <CustomDrawer customStyle={{
                            padding: 5,
                            height: '100vh',
                        }}>
                            <Servico />
                        </CustomDrawer>
                    </Route>
                    <Route path={match.path}>
                        <CustomDrawer >
                            <Home />
                        </CustomDrawer>
                    </Route>
                </Switch>
                <Backdrop />
                </LoadContext.Provider>
        );
}

export default Dashboard;
