import React from 'react';

import {
  WrapperHeader,
  WrapperAdd,
} from './styles';

import { 
  Button,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  ListItemText,
  ListItemAvatar,
  TextField,
  Avatar,
} from '@material-ui/core';


import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import EmailIcon from '@material-ui/icons/Email';
import PhoneIcon from '@material-ui/icons/Phone';
import RoomIcon from '@material-ui/icons/Room';
import PersonIcon from '@material-ui/icons/Person';
import WorkIcon from '@material-ui/icons/Work';
import InstagramIcon from '@material-ui/icons/Instagram';
import FacebookIcon from '@material-ui/icons/Facebook';
import FaceIcon from '@material-ui/icons/Face';

import { APIURL } from '../../environment';

import { useGenericModel, useSnackbarsAlert, useBackdrop } from '../../hooks';
import { atualiza } from '../../utils/Services';

import SaveIcon from '@material-ui/icons/Save';
import Fab from '@material-ui/core/Fab';
import Zoom from '@material-ui/core/Zoom';
import { makeStyles, useTheme } from '@material-ui/core/styles';

const Perfil = () => {

  const theme = useTheme();

  const [perfil, setPerfil, PerfilInicial, addPerfil, rmPerfil, atuPerfil, upPerfil] = useGenericModel('account_profile');
  const [openALert, closeAlert, Alert] = useSnackbarsAlert()
  const [openBackdro, closeBackdro, Backdro] = useBackdrop()
  const [input, setInput] = React.useState({})

  React.useEffect(() => {
    let perfilInicial = {...perfil}
    delete perfilInicial.avatar;
    setInput(perfilInicial)
  },[perfil]);


  const list_validae = {
    text: (value) =>  { return { value: true, msg: "" } },
    email: (value) => { return { value: true, msg: "" } },
    telefone: (value) => { return { value: true, msg: "" } }
  }

  const validaInput = (value, type, key, customObj, customKey) => {
      let result = list_validae[type](value)
      if( result.value ){
          if(customObj){
              customObj[customKey] = value
              let novoInput = {...input}
              novoInput[key] = customObj
              setInput(novoInput)
          }else{
              let novoInput = {...input}
              novoInput[key] = value
              setInput(novoInput)
          }
      }else {
        console.log(result.msg)
      }
  }

  async function enviaFormularioProfile(){
    openBackdro()
    let novoInput = {...input}
    let result = await atualiza('account_profile', novoInput)
    setTimeout(() => {
      if ( result.hasOwnProperty("id") ) {
          setPerfil(result)
          openALert({
            msg: "Perfil atualizado",
            severity: "success"
          })
      }else{
          openALert({
            msg: "Solicitação não pode ser concluida",
            severity: "error"
          })
      }
      closeBackdro()
    }, 300);
}

  return (
    <div>
          {/* <WrapperHeader >
              <WrapperAdd >
                  <Button variant="contained" color="primary" onClick={enviaFormularioProfile} >
                      Atualizar
                  </Button>
              </WrapperAdd>
          </WrapperHeader> */}

                <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-avatar"
                      >
                        <ListItemAvatar>
                          {
                            perfil?.avatar ? 
                              <Avatar alt={input.user?.first_name} src={`${APIURL}${perfil?.avatar}`} style={{ width: 40, height: 40}} />
                            :
                              <FaceIcon />
                          }
                        </ListItemAvatar>
                          <ListItemText primary="Avatar" secondary="" />
                    </AccordionSummary>
                    <AccordionDetails>
                      <input type="file" onChange={ 
                        (event) => {
                          let novoInput = {...input}
                          novoInput['avatar'] = event.target.files[0]
                          setInput(novoInput)
                        }
                    } /> 
                    </AccordionDetails>
                </Accordion>

                <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        id="acordion-nome"
                      >
                        <ListItemAvatar>
                            <PersonIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Nome" secondary={input.user?.first_name} />
                    </AccordionSummary>
                    <AccordionDetails>
                      <TextField 
                        fullWidth 
                        id="input-nome" 
                        label={ input.user?.first_name ? false : "Nome" } 
                        variant="outlined" 
                        size="small"
                        value={ input.user?.first_name ? input.user?.first_name : "" }
                        onChange={ (event)=> validaInput( event.target.value, "text", "user", input.user, "first_name" )} 
                      /> 
                    </AccordionDetails>
                </Accordion>

                <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        id="acordion-profissao"
                      >
                        <ListItemAvatar>
                            <WorkIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Profissão" secondary={input?.profissao} />
                    </AccordionSummary>
                    <AccordionDetails>
                      <TextField 
                        fullWidth 
                        id="input-profissao" 
                        label={ input?.profissao ? false : "Profissão" } 
                        variant="outlined" 
                        size="small"
                        value={input?.profissao ?  input.profissao : "" }
                        onChange={ (event)=> validaInput( event.target.value, "text", "profissao" ) } 
                        /> 
                    </AccordionDetails>
                </Accordion>

                <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-email"
                      >
                        <ListItemAvatar>
                            <EmailIcon />
                        </ListItemAvatar>
                        <ListItemText primary="E-mail" secondary={ input.user?.email } />
                    </AccordionSummary>
                    <AccordionDetails>
                      <TextField 
                        fullWidth 
                        id="input-email" 
                        label={ input.user?.email ? false : "E-mail" } 
                        variant="outlined" 
                        size="small" 
                        value={ input.user?.email ?  input.user?.email : "" }
                        onChange={ (event)=> validaInput( event.target.value, "email", "user", input.user, "email" ) }                        
                        /> 
                    </AccordionDetails>
                </Accordion>
  
                <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-telefone"
                      >
                      <ListItemAvatar>
                          <PhoneIcon />
                      </ListItemAvatar>
                      <ListItemText primary="Telefone" secondary={input?.telefone} />
                    </AccordionSummary>
                    <AccordionDetails> 
                    <TextField 
                      fullWidth 
                      id="input-telefone" 
                      label={ input?.telefone ? false : "Telefone" } 
                      variant="outlined" 
                      size="small"
                      value={ input?.telefone ? input?.telefone: "" }
                      onChange={ (event)=> validaInput( event.target.value, "telefone", "telefone" ) }                        
                      /> 
                    </AccordionDetails>
                </Accordion>              

              <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-endereco"
                      >
                      <ListItemAvatar>
                          <RoomIcon />
                      </ListItemAvatar>
                      <ListItemText primary="Endereço" secondary={ input?.endereco } />
                    </AccordionSummary>
                    <AccordionDetails> 
                    <TextField 
                      fullWidth 
                      id="input-endereco" 
                      label={ input?.endereco ? false : "Endereço" }
                      variant="outlined" 
                      size="small"
                      value={ input?.endereco ? input?.endereco : "" }
                      onChange={ (event)=> validaInput( event.target.value, "text", "endereco" ) }                         
                      /> 
                    </AccordionDetails>
                </Accordion>                

              <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-instagram"
                      >
                      <ListItemAvatar>
                          <InstagramIcon />
                      </ListItemAvatar>
                      <ListItemText primary="Instagram" secondary={ input?.instagram } />
                    </AccordionSummary>
                    <AccordionDetails> 
                    <TextField 
                        fullWidth 
                        id="input-instagram" 
                        label={ input?.instagram ? false : "Instagram" }  
                        variant="outlined" 
                        size="small" 
                        value={ input?.instagram ? input?.instagram : "" }
                        onChange={ (event)=> validaInput( event.target.value, "text", "instagram" ) }                      
                      /> 
                    </AccordionDetails>
                </Accordion> 

              <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="acordion-facebook"
                      >
                      <ListItemAvatar>
                          <FacebookIcon />
                      </ListItemAvatar>
                      <ListItemText primary="Facebook" secondary={ input?.facebook } />
                    </AccordionSummary>
                    <AccordionDetails> 
                    <TextField 
                        fullWidth 
                        id="input-facebook" 
                        label={ input?.facebook ? false : "Facebook" } 
                        variant="outlined" 
                        size="small" 
                        value={ input?.facebook ? input?.facebook : "" }
                        onChange={ (event)=> validaInput( event.target.value, "text", "facebook" ) }                          
                        /> 
                    </AccordionDetails>
                </Accordion> 
                <Alert />
                <Backdro />

        <Zoom
            key="primary"
            in={1}
            timeout={{
              enter: theme.transitions.duration.enteringScreen,
              exit: theme.transitions.duration.leavingScreen,
            }}
            style={{
              transitionDelay: `${theme.transitions.duration.leavingScreen}ms`,
            }}
            unmountOnExit
          >
            <Fab 
              onClick={enviaFormularioProfile}
              aria-label="addicon" 
              color="primary"
              style={{
                position: 'fixed',
                bottom: '30px',
                right: '30px',
                backgroundImage: 'linear-gradient(to right, #8f00dd , #6866ff)',
              }}
            >
              <SaveIcon />
            </Fab>
        </Zoom>


    </div>

  );

}

export { Perfil }