import React from 'react';

import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';

import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';

import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import TodayIcon from '@material-ui/icons/Today';
import MoreVertIcon from '@material-ui/icons/MoreVert';

import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';


import { useGenericModel, useSnackbarsAlert, useBackdrop } from '../../hooks';

import { Calendar, momentLocalizer } from 'react-big-calendar'
import moment from 'moment'

import { 
  WrapperButton,
} from './styles';

const localizer = momentLocalizer(moment)

const Agenda = () => {

  const [eventos, setEventos, EventosInicial, addEventos, rmEventos, putEventos, upEventos] = useGenericModel('account_events');
  const [listEvents, setListEvents] = React.useState([])

  React.useEffect(()=>{
      criaListaEventos(eventos)
  },[eventos])

  function criaListaEventos(eventos){
      let lista = eventos.map( evento => {
            let start = new Date(evento.dia)
            let [hours, minute] = evento.servico.tempo_atividade.split(":")
            let end = new Date( moment(start).add(hours, 'hours').add(minute, 'minute').format() )

            return {   
              title: `${evento.servico.nome} - ${evento.cliente.first_name}`,
              start: new Date(start.getFullYear(), start.getMonth(), start.getDate(), start.getHours() + 1, start.getMinutes()),
              end: new Date(end.getFullYear(), end.getMonth(), end.getDate(), end.getHours() + 1, end.getMinutes()),
              allDay: false,
              resource: [],
              evento: evento
          }
      })
    setListEvents(lista)
  }

  const customSlotPropGetter = date => {
      return {
        style: {
            height: '200px',
        },
      }
  }


  return (
        <Calendar
            localizer={localizer}
            events={listEvents}
            startAccessor="start"
            endAccessor="end"
            style={{ height: 'calc(100vh - 64px)' }}
            defaultView="day"
            scrollToTime={new Date()}
            toolbar={true}
            components={{
              toolbar: ButtonAppBar,
            }}
        />
  );
}

const useStyles = makeStyles((theme) => ({
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 230,
  },
  button: {
    minWidth: 130
  }
}));

export default function ButtonAppBar(props) {
  const classes = useStyles();

  const { onNavigate, views, view, onView, date } = props
  
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const selectView = (view) => {
    onView(view)
    setAnchorEl(null);
  };

  React.useEffect(()=>{ 
      console.log(props)
  },[])

  return (
      <AppBar elevation={0} position="static" style={{
          background: "#ffffff",
          color: "rgba(0, 0, 0, 0.60)",
      }}>
        <Toolbar>
          <IconButton color="inherit" aria-label="back" onClick={() => onNavigate('PREV') } >
            <ArrowBackIosIcon />
          </IconButton>
          <IconButton color="inherit" aria-label="today" onClick={() => onNavigate('TODAY') } >
            <TodayIcon />
          </IconButton>
          <IconButton color="inherit" aria-label="back" onClick={() => onNavigate('NEXT') } >
            <ArrowForwardIosIcon />
          </IconButton>
          <Typography variant="button" className={classes.title}>
              {/* { moment(date).format("DD/MM/YYYY") } */}
          </Typography>
            {/* <Button variant="outlined" size="medium" onClick={handleClick} className={classes.button} endIcon={<ArrowDropDownIcon />} >
              {view}
            </Button> */}
            <IconButton
              aria-label="more"
              aria-controls="long-menu"
              aria-haspopup="true"
              onClick={handleClick}
            >
              <MoreVertIcon />
            </IconButton>
            <Menu
              id="simple-menu"
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={handleClose}
            >
              {
                views.map( item => (
                  <MenuItem onClick={ () => selectView(item) }>{item}</MenuItem>
                ))
              }
            </Menu>
        </Toolbar>
      </AppBar>
  );
}


export { Agenda }