import React from 'react';
import { TwitterPicker, ChromePicker } from 'react-color';

import {
  Container,
  WrapperInput,
  WrapperCard,
  Card,
  Logo,
  WrapperHeader,
  WrapperAdd,
  ButtonUploadBox,
} from './styles';

import { 
  Button,
} from '@material-ui/core';

import { useGenericModel, useSnackbarsAlert, useBackdrop } from '../../hooks';
import { APIURL } from '../../environment';
import { atualiza } from '../../utils/Services';

import AddPhotoAlternateIcon from '@material-ui/icons/AddPhotoAlternate';

import SaveIcon from '@material-ui/icons/Save';
import Fab from '@material-ui/core/Fab';
import Zoom from '@material-ui/core/Zoom';
import { makeStyles, useTheme } from '@material-ui/core/styles';

//https://casesandberg.github.io/react-color/
const Cartao = () => {

  const [cartao, setCartao, cartaoInicial] = useGenericModel('account_card');
  const [displayColorPicker, setDisplayColorPicker] = React.useState(false)
  const [input, setInput] = React.useState({})

  const hiddenFileInput = React.useRef(null);
  const [file, setFile] = React.useState(null);

  const [openALert, closeAlert, Alert] = useSnackbarsAlert()
  const [openBackdro, closeBackdro, Backdro] = useBackdrop()

  const theme = useTheme();

  React.useEffect(() => {
      setInput(cartao)
  },[cartao]);

  const handleChangeColor = (color) => {
      let novoInput = {...input}
      novoInput['cor'] = color.hex
      setInput(novoInput)
  }

  const handleChangeFile = (event) => {
      setFile(URL.createObjectURL(event.target.files[0]))
      let novoInput = {...input}
      novoInput['logo'] = event.target.files[0]
      setInput(novoInput)     
  }

  async function enviaFormularioProfile(){
    openBackdro()
    let novoInput = {...input}
    if(file){
      setFile(null)
    }else{
      delete novoInput.logo;
    }
    let result = await atualiza('account_card', novoInput)
    setTimeout(() => {
      if ( result.hasOwnProperty("cor") ) {
          setCartao(result)
          openALert({
            msg: "Cartão atualizado",
            severity: "success"
          })
      }else{
          openALert({
            msg: "Solicitação não pode ser concluida",
            severity: "error"
          })
      }
      closeBackdro()
    }, 300);

}

  const handleClick = () => {
    setDisplayColorPicker(!displayColorPicker)
  };

  const handleClose = () => {
    setDisplayColorPicker(false)
  };

  const handleClickFoto = event => {
    setDisplayColorPicker(false)
    hiddenFileInput.current.click();
  };

  return (
    <Container >

        {/* <WrapperHeader >
              <WrapperAdd >
                  <Button variant="contained" color="primary" onClick={enviaFormularioProfile} >
                      Atualizar
                  </Button>
              </WrapperAdd>
          </WrapperHeader>     */}

        <WrapperCard >
          <Card color={ input?.cor ? input?.cor : '#8ED1FC' }  onClick={handleClick}>
                  { 
                    displayColorPicker ? 
                        <div style={{
                              position: 'absolute',
                              top: '15px',
                            }} 
                          onClick={handleClose}>
                              <TwitterPicker
                                triangle="hide"
                                color={ input?.cor ? input?.cor : '#8ED1FC' }
                                onChangeComplete={handleChangeColor}
                              />
                        </div> 
                    : 
                    null 
                  }
                <ButtonUploadBox onClick={handleClickFoto} isBorder={  file || input?.logo ? false : true } >
                    { 
                      file ?
                          <Logo src={file}/>
                        :  
                          input?.logo ? 
                            <Logo src={`${APIURL}${input?.logo}`}/>
                          :
                            <AddPhotoAlternateIcon /> 
                          
                    }
                </ButtonUploadBox>
                <input type="file"
                        onChange={handleChangeFile}
                        ref={hiddenFileInput}
                        style={{display:'none'}} 
                /> 

          </Card>
        </WrapperCard>
        <Alert />
        <Backdro />

        <Zoom
            key="primary"
            in={1}
            timeout={{
              enter: theme.transitions.duration.enteringScreen,
              exit: theme.transitions.duration.leavingScreen,
            }}
            style={{
              transitionDelay: `${theme.transitions.duration.leavingScreen}ms`,
            }}
            unmountOnExit
          >
            <Fab 
              onClick={enviaFormularioProfile}
              aria-label="addicon" 
              color="primary"
              style={{
                position: 'fixed',
                bottom: '30px',
                right: '30px',
                backgroundImage: 'linear-gradient(to right, #8f00dd , #6866ff)',
              }}
            >
              <SaveIcon />
            </Fab>
        </Zoom>

    </Container>
  );

}

export { Cartao }