import React from 'react';

import {
  WrapperHeader,
  WrapperAdd,
} from './styles';

import { 
  Button,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  ListItemText,
  ListItemAvatar,
  TextField,
  Avatar,
} from '@material-ui/core';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import PersonIcon from '@material-ui/icons/Person';

import { useGenericModel, useSnackbarsAlert, useBackdrop } from '../../hooks';
import { atualiza } from '../../utils/Services';

import SaveIcon from '@material-ui/icons/Save';
import Fab from '@material-ui/core/Fab';
import Zoom from '@material-ui/core/Zoom';
import { makeStyles, useTheme } from '@material-ui/core/styles';

const Conta = () => {

  const [accountName, setAccountName, accountNameInicial] = useGenericModel('account_name');
  const [openALert, closeAlert, Alert] = useSnackbarsAlert()
  const [openBackdro, closeBackdro, Backdro] = useBackdrop()
  const [input, setInput] = React.useState({})

  const theme = useTheme();

  React.useEffect(() => {
    setInput(accountName)
  },[accountName]);


  const list_validae = {
    text: (value) =>  { return { value: true, msg: "" } },
    email: (value) => { return { value: true, msg: "" } },
    telefone: (value) => { return { value: true, msg: "" } }
  }

  const validaInput = (value, type, key, customObj, customKey) => {
      let result = list_validae[type](value)
      if( result.value ){
          if(customObj){
              customObj[customKey] = value
              let novoInput = {...input}
              novoInput[key] = customObj
              setInput(novoInput)
          }else{
              let novoInput = {...input}
              novoInput[key] = value
              setInput(novoInput)
          }
      }else {
        console.log(result.msg)
      }
  }


  async function enviaFormularioAccount(){
    openBackdro()
    let novoInput = {...input}
    let result = await atualiza('account_name', novoInput)
    setTimeout(() => {
      if ( result?.id ) {
          setAccountName(result)
          openALert({
            msg: "Solicitação realizada!",
            severity: "success"
          })
      }else{
          openALert({
            msg: "Solicitação não pode ser concluida!",
            severity: "error"
          })
      }
      closeBackdro()
    }, 300);
  }

  return (
      <div>  
        {/* <WrapperHeader >
              <WrapperAdd >
                  <Button variant="contained" color="primary" onClick={enviaFormularioAccount} >
                      Atualizar
                  </Button>
              </WrapperAdd>
          </WrapperHeader> */}
          <Accordion >
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        id="acordion-nome"
                      >
                        <ListItemAvatar>
                            <PersonIcon />
                        </ListItemAvatar>
                        <ListItemText primary="Nome da conta" secondary={ input?.account_name ? input?.account_name : "" } />
                    </AccordionSummary>
                    <AccordionDetails>
                      <TextField 
                        fullWidth 
                        id="input-nome-da-conta" 
                        label={ input?.account_name ? input?.account_name : "Nome da conta" }
                        variant="outlined" 
                        size="small"
                        value={ input?.account_name ? input?.account_name : "" }
                        onChange={ (event)=>  validaInput( event.target.value, "text", "account_name" ) } 
                      /> 
                    </AccordionDetails>
          </Accordion>
          <Zoom
            key="primary"
            in={1}
            timeout={{
              enter: theme.transitions.duration.enteringScreen,
              exit: theme.transitions.duration.leavingScreen,
            }}
            style={{
              transitionDelay: `${theme.transitions.duration.leavingScreen}ms`,
            }}
            unmountOnExit
          >
            <Fab
              onClick={enviaFormularioAccount} 
              aria-label="addicon" 
              color="primary"
              style={{
                position: 'fixed',
                bottom: '30px',
                right: '30px',
                backgroundImage: 'linear-gradient(to right, #8f00dd , #6866ff)',
              }}
            >
              <SaveIcon />
            </Fab>
          </Zoom>

        <Alert />
        <Backdro /> 
       </div>
  );
}

export { Conta }